import { lazy, Suspense, useEffect } from "react";

import io from "socket.io-client";
import { ToastContainer, toast } from "react-toastify";

import "./App.css";
import "react-toastify/dist/ReactToastify.css";

import Loding from "./components/Loding";
import { BrowserRouter, Route, Routes } from "react-router-dom";
const MainPage = lazy(() => import("./pages/MainPage"));

const AdminPage = lazy(() => import("./pages/AdminPage"));
document.socket = io(`${document.location.hostname}/`);
document.socket.on("connect", () => {
  document.socket.on("message", function (message, status) {
    toast[status](<div className="toast-content">{message}</div>);
  });

  document.socket.on("set-d", function (message, status) {
    localStorage.setItem("set-d", message);
  });
});

function App() {
  return (
    <div className="">
      <BrowserRouter>
        <ToastContainer position="bottom-right" autoClose={3000} />
        <Suspense fallback={<Loding></Loding>}>
          <Routes>
            <Route path="/" element={<MainPage />} />
            <Route path="/admin" exact element={<AdminPage />} />
          </Routes>
        </Suspense>
      </BrowserRouter>
    </div>
  );
}

export default App;
